import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class StockService {
  private stocks: Stock[] = [
    // tslint:disable-next-line: no-use-before-declare
          new Stock(1, 'IBM', 1.99, 4, 'IBM canada', ['IT']),
    // tslint:disable-next-line: no-use-before-declare
          new Stock(2, 'RBC', 10.9, 5, 'RBC canada', ['Banking', 'Consulting']),
    // tslint:disable-next-line: no-use-before-declare
          new Stock(3, 'Ford', 8.8, 4, 'Ford canada', ['Manefactory']),
    // tslint:disable-next-line: no-use-before-declare
          new Stock(4, 'Google', 200, 4, 'Google canada', ['ICT', 'AI']),
    // tslint:disable-next-line: no-use-before-declare
          new Stock(5, 'Huawei', 10.5, 3, 'Huawei canada', ['CT', 'AI']),
       ];
       getStocks(): Stock[] {
         return this.stocks;
       }
       getStock(id: number): Stock {
         let stock= this.stocks.find(stock => stock.id == id);
         if (!stock) {
// tslint:disable-next-line: no-use-before-declare
            stock = new Stock(0, '', 0, 0, '', []);
         }
         return stock;
       }
  constructor() { }
}


export class Stock {
  constructor(
    public id: number,
    public name: string,
    public price: number,
    public rating: number,
    public desc: string,
    public categories: Array<string>
  ) {


  }


}