import { Component, OnInit } from '@angular/core';
import { Stock, StockService } from '../stock.service';
import { ActivatedRoute, Route, Router } from '@angular/router';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';

@Component({
  selector: 'app-stock-form',
  templateUrl: './stock-form.component.html',
  styleUrls: ['./stock-form.component.css']
})
export class StockFormComponent implements OnInit {
  formModel: FormGroup;
  stock: Stock;
  cate = ['IT', 'ICT', 'Manefactory', 'AI', 'Banking', 'Consulting'];
  constructor(private routeinfo: ActivatedRoute, private stockservice: StockService, private router: Router) { }

  ngOnInit() {
    // tslint:disable-next-line:no-string-literal
    const stockid = this.routeinfo.snapshot.params['id'];
    this.stock = this.stockservice.getStock(stockid);
    let fb = new FormBuilder();
    this.formModel = fb.group(
      {
       Name: [this.stock.name, [Validators.required, Validators.minLength(3)]],
       Price: [this.stock.price, [Validators.required]],
       Desc: [this.stock.desc],
       cate: fb.array(
         [
           new FormControl(this.stock.categories.indexOf(this.cate[0]) !== -1),
           new FormControl(this.stock.categories.indexOf(this.cate[1]) !== -1),
           new FormControl(this.stock.categories.indexOf(this.cate[2]) !== -1),
           new FormControl(this.stock.categories.indexOf(this.cate[3]) !== -1),
           new FormControl(this.stock.categories.indexOf(this.cate[4]) !== -1),
           new FormControl(this.stock.categories.indexOf(this.cate[5]) !== -1)
         ]
       )
      }
    );
  }

  cancel() {
    this.router.navigateByUrl('/stock');
  }
  save() {
    this.formModel.value.rating = this.stock.rating;
    var chinese = [];
    var index = 0;
    for(let i = 0; i < 6; i++){
      if(this.formModel.value.cate[i]){
        chinese[index] = this.cate[i];
      }
    }
    this.formModel.value.cate = chinese;
    console.log(this.stock.rating);
    this.router.navigateByUrl('/stock');
  }
}
